<template>
  <div class="page-task-send">
    <el-steps :active="2" :space="200">
      <el-step title="第一步" description="确认发送目标"></el-step>
      <el-step title="第二步" description="确认发送的内容与时间"></el-step>
    </el-steps>
    <el-row :gutter="10">
      <!-- left -->
      <el-col :span="10">
        <h3>主题预览</h3>
      </el-col>
      <!-- right -->
      <el-col :span="8" class="layout-right">
        <div class="right-content">
          <el-form label-position="top" label-width="80px">
            <el-form-item label="发送主题">
              <el-input v-model="sentTheme" type="textarea"
                placeholder="可一次性输入多个主题，用分号隔开"></el-input>
            </el-form-item>
            <el-form-item label="发送模板">
              <el-select v-model="tplValue">
                <el-option v-for="(item, index) in options"
                  :value="item.value"
                  :label="item.label"
                  :key="index">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="试发送邮箱">
              <el-input type="textarea" placeholder="可同时发送多个"></el-input>
            </el-form-item>
            <el-form-item label="试发送邮箱">
              <el-input :disabled="true" v-model="sendEmail"></el-input>
            </el-form-item>
            <el-form-item label="试发送简历ID">
              <el-input :disabled="true" v-model="sendPosID"></el-input>
            </el-form-item>
          </el-form>
          <el-button class="btn-try" type="primary">试发送</el-button>
        </div>
        <el-button 
          :disabled="!sendBtnActive" class="btn-send">立即发送</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        options: [
          { label: '标准版EDM模板', value: 0 }
        ],
        tplValue: 0,
        sendEmail: 'abc@123.com',
        sendPosID: '234',
        sentTheme: '',
        trySendSucc: false,
      }
    },
    computed: {
      sendBtnActive(){
        return this.trySendSucc && !!this.sentTheme
      }
    }
  }
</script>

<style lang="stylus">
  @import './Task-Send.styl'
</style>


